document.addEventListener('DOMContentLoaded', () => {
  /* ---------------------------------------
   * DEBUG HELPERS (LOG ONLY NOT FOUND)
   * ------------------------------------- */
  const warnOnceCache = new Set();

  function warnOnce(key, message) {
    if (warnOnceCache.has(key)) return;
    warnOnceCache.add(key);
    console.warn(message);
  }

  function logIfNotFound(label, el) {
    if (!el) {
      warnOnce(label, `[GSAP NOT FOUND] ${label}`);
      return true;
    }
    return false;
  }

  /* ---------------------------------------
   * DOM ELEMENTS
   * ------------------------------------- */
  const gallery = document.querySelector('.gallery');
  const galleryContainer = document.querySelector('.gallery-container');
  const cardsNode = document.querySelectorAll('.card');

  logIfNotFound('.gallery', gallery);
  logIfNotFound('.gallery-container', galleryContainer);

  if (cardsNode.length === 0) {
    warnOnce('.card-empty', '[GSAP NOT FOUND] .card (no elements found)');
  }

  /* ---------------------------------------
   * STATE
   * ------------------------------------- */
  const cards = [];
  const transformState = [];

  const isPreviewActive = false;
  const isTransitioning = false;

  const config = {
    radius: 0,
    sensitivity: 500,
    effectFalloff: 250,
    cardMoveAmount: 50,
    lerpFactor: 0.15,
    isMobile: window.innerWidth < 1000,
    imageCount: cardsNode.length,
  };

  // === AUTO RADIUS (16 images friendly) ===
  const IMAGE_COUNT = 16;
  const GAP_EM = 3;

  const rootFontSize = parseFloat(getComputedStyle(document.documentElement).fontSize);

  const cardWidth = cardsNode[0]?.getBoundingClientRect().width || 0;
  const gapPx = GAP_EM * rootFontSize;

  config.imageCount = IMAGE_COUNT;

  config.radius = ((cardWidth + gapPx) * IMAGE_COUNT) / (2 * Math.PI);

  const parallaxState = {
    targetX: 0,
    targetY: 0,
    targetZ: 0,
    currentX: 0,
    currentY: 0,
    currentZ: 0,
  };

  /* ---------------------------------------
   * INIT CARDS
   * ------------------------------------- */
  for (let i = 0; i < config.imageCount; i++) {
    const card = cardsNode[i];

    if (!card) {
      warnOnce(`.card-${i}`, `[GSAP NOT FOUND] .card at index ${i}`);
      continue;
    }

    const angle = (i / config.imageCount) * Math.PI * 2;
    const x = config.radius * Math.cos(angle);
    const y = config.radius * Math.sin(angle);

    card.dataset.index = i;

    gsap.set(card, {
      x,
      y,
      rotation: (angle * 180) / Math.PI + 90,
      transformPerspective: 800,
      transformOrigin: 'center center',
    });

    cards.push(card);
    transformState.push({
      angle,
      currentRotation: 0,
      targetRotation: 0,
      currentX: 0,
      targetX: 0,
      currentY: 0,
      targetY: 0,
      currentScale: 1,
      targetScale: 1,
    });
  }

  /* ---------------------------------------
   * RESIZE
   * ------------------------------------- */
  function handleResize() {
    const w = window.innerWidth;
    config.isMobile = w < 1000;

    let scale = 1;
    if (w < 768) scale = 0.6;
    else if (w < 1200) scale = 0.8;

    if (!logIfNotFound('.gallery', gallery)) {
      gsap.set(gallery, { scale });
    }

    if (!isPreviewActive) {
      Object.assign(parallaxState, {
        targetX: 0,
        targetY: 0,
        targetZ: 0,
        currentX: 0,
        currentY: 0,
        currentZ: 0,
      });

      transformState.forEach((state) => {
        state.currentRotation = 0;
        state.targetRotation = 0;
        state.currentScale = 1;
        state.targetScale = 1;
        state.currentX = 0;
        state.targetX = 0;
        state.currentY = 0;
        state.targetY = 0;
      });
    }
  }

  window.addEventListener('resize', handleResize);
  handleResize();

  /* ---------------------------------------
   * MOUSE MOVE
   * ------------------------------------- */
  document.addEventListener('mousemove', (e) => {
    if (isPreviewActive || isTransitioning || config.isMobile) return;

    const cx = window.innerWidth / 2;
    const cy = window.innerHeight / 2;
    const px = (e.clientX - cx) / cx;
    const py = (e.clientY - cy) / cy;

    parallaxState.targetY = px * 15;
    parallaxState.targetX = -py * 15;
    parallaxState.targetZ = (px + py) * 5;

    cards.forEach((card, i) => {
      const state = transformState[i];
      if (!card || !state) {
        warnOnce(`mousemove-${i}`, `[GSAP SKIPPED] mousemove index ${i}`);
        return;
      }

      const rect = card.getBoundingClientRect();
      const dx = e.clientX - (rect.left + rect.width / 2);
      const dy = e.clientY - (rect.top + rect.height / 2);
      const dist = Math.hypot(dx, dy);

      if (dist < config.sensitivity) {
        const f = Math.max(0, 1 - dist / config.effectFalloff);
        const move = config.cardMoveAmount * f;

        state.targetRotation = 180 * f;
        state.targetScale = 1 + 0.3 * f;
        state.targetX = move * Math.cos(state.angle);
        state.targetY = move * Math.sin(state.angle);
      } else {
        state.targetRotation = 0;
        state.targetScale = 1;
        state.targetX = 0;
        state.targetY = 0;
      }
    });
  });

  /* ---------------------------------------
   * MOUSE OUT
   * ------------------------------------- */
  document.addEventListener('mouseout', (e) => {
    if (!e.relatedTarget && !isPreviewActive && !isTransitioning) {
      transformState.forEach((state) => {
        state.targetRotation = 0;
        state.targetScale = 1;
        state.targetX = 0;
        state.targetY = 0;
      });

      parallaxState.targetX = 0;
      parallaxState.targetY = 0;
      parallaxState.targetZ = 0;
    }
  });

  /* ---------------------------------------
   * RAF ANIMATE
   * ------------------------------------- */
  function animate() {
    if (!isPreviewActive && !isTransitioning) {
      parallaxState.currentX +=
        (parallaxState.targetX - parallaxState.currentX) * config.lerpFactor;
      parallaxState.currentY +=
        (parallaxState.targetY - parallaxState.currentY) * config.lerpFactor;
      parallaxState.currentZ +=
        (parallaxState.targetZ - parallaxState.currentZ) * config.lerpFactor;

      if (!logIfNotFound('.gallery-container', galleryContainer)) {
        gsap.set(galleryContainer, {
          rotateX: parallaxState.currentX,
          rotateY: parallaxState.currentY,
          rotation: parallaxState.currentZ,
          transformOrigin: 'center center',
        });
      }

      cards.forEach((card, i) => {
        const state = transformState[i];
        if (!card || !state) {
          warnOnce(`animate-${i}`, `[GSAP SKIPPED] animate index ${i}`);
          return;
        }

        state.currentRotation += (state.targetRotation - state.currentRotation) * config.lerpFactor;
        state.currentScale += (state.targetScale - state.currentScale) * config.lerpFactor;
        state.currentX += (state.targetX - state.currentX) * config.lerpFactor;
        state.currentY += (state.targetY - state.currentY) * config.lerpFactor;

        const x = config.radius * Math.cos(state.angle);
        const y = config.radius * Math.sin(state.angle);

        gsap.set(card, {
          x: x + state.currentX,
          y: y + state.currentY,
          rotationY: state.currentRotation,
          scale: state.currentScale,
          rotation: (state.angle * 180) / Math.PI + 90,
          transformPerspective: 1000,
          transformOrigin: 'center center',
        });
      });
    }

    requestAnimationFrame(animate);
  }

  animate();
});
